  margin-bottom: 5vh;
  text-align: center;
  width: 70%;
}

.servicesFront div {
  width: 100%;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 22rem 22rem 22rem;
      grid-template-columns: 22rem 22rem 22rem;
  justify-content: center;
  align-items: center;
}

.servicesFront a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: black;
}

.servicesFront ul {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
